/*------------------------------------------------------------------
2.0 - Home Page
------------------------------------------------------------------*/
#homeContent {
  grid-area: content;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 150px 120px 190px 0px;
  /*------------------------------------------------------------------
    2.1 - Play Button
    ------------------------------------------------------------------*/
  /*------------------------------------------------------------------
    2.2 - Connections Informations
    ------------------------------------------------------------------*/
  /*------------------------------------------------------------------
    2.3 - Log Preview
    ------------------------------------------------------------------*/ }
  #homeContent #buttonContainer {
    display: flex;
    justify-content: center;
    align-items: center; }
    #homeContent #buttonContainer .startStopAnimation::before {
      left: 20% !important;
      opacity: 0 !important; }
    #homeContent #buttonContainer .startStopAnimation::after {
      left: 50% !important;
      opacity: 1 !important; }
    #homeContent #buttonContainer #startStopButton {
      margin-top: 20px;
      height: 70px;
      width: 70px;
      border-radius: 50%;
      font-size: 20px;
      position: relative; }
      #homeContent #buttonContainer #startStopButton::before, #homeContent #buttonContainer #startStopButton::after {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        top: 50%;
        transform: translate(-50%, -50%);
        transition: all .2s ease-in-out; }
      #homeContent #buttonContainer #startStopButton::before {
        content: attr(data-start);
        position: absolute;
        left: 50%;
        color: #15947a; }
      #homeContent #buttonContainer #startStopButton::after {
        content: attr(data-stop);
        position: absolute;
        left: 80%;
        opacity: 0;
        color: #cc3d2d; }
  #homeContent #infoContainer {
    display: flex;
    justify-content: space-around;
    align-items: center; }
    #homeContent #infoContainer #leftInfo p,
    #homeContent #infoContainer #rightInfo p {
      color: white;
      text-align: center; }
    #homeContent #infoContainer #leftInfo hr,
    #homeContent #infoContainer #rightInfo hr {
      margin: 10px auto;
      width: 10px;
      border: 0.5px solid rgba(100, 100, 100, 0.5); }
  #homeContent #logPreviewContainer {
    position: relative;
    height: 190px;
    min-width: 360px;
    width: 70%;
    max-width: 600px;
    border: 2px dashed grey;
    border-radius: 5px;
    margin: auto;
    background-color: rgba(128, 128, 128, 0.03);
    color: white; }
    #homeContent #logPreviewContainer #logPreview {
      overflow: auto;
      height: 100%;
      width: 100%;
      padding: 15px; }
      #homeContent #logPreviewContainer #logPreview p {
        margin: 5px 0px; }
        #homeContent #logPreviewContainer #logPreview p.endSession {
          margin-bottom: 20px; }
        #homeContent #logPreviewContainer #logPreview p.animated_waiter {
          color: #ffd866; }
          #homeContent #logPreviewContainer #logPreview p.animated_waiter::after {
            content: ' ';
            animation: animation_waiter 4s infinite; }
        #homeContent #logPreviewContainer #logPreview p span.time {
          color: grey;
          font-size: 13px;
          margin-left: 10px; }
    #homeContent #logPreviewContainer #actionsButtons {
      position: absolute;
      top: 8px;
      right: 10px; }
      #homeContent #logPreviewContainer #actionsButtons #resetLogsButton {
        background: none;
        color: grey; }
        #homeContent #logPreviewContainer #actionsButtons #resetLogsButton:hover {
          color: white; }

/*------------------------------------------------------------------
3.0 - Keyframes
------------------------------------------------------------------*/
@keyframes animation_waiter {
  25% {
    content: ' .'; }
  50% {
    content: ' ..'; }
  75% {
    content: ' ...'; }
  100% {
    content: ''; } }
